"use client"
import logo1 from "@/public/assets/TimeLineLogo/Logo1.svg"
import logo2 from "@/public/assets/TimeLineLogo/Logo2.svg"
import logo3 from "@/public/assets/TimeLineLogo/Logo3.svg"
import logo4 from "@/public/assets/TimeLineLogo/Logo4.svg"
import Image from "next/image"
const TimeLine = () => {
    const timelineData = [
        {
            logo: logo1,
            heading: "Leadership",
            description: "Fully committed to the success company",
        },
        {
            logo: logo2,
            heading: "Responsibility",
            description: "Students will always be our top priority",
        },
        {
            logo: logo3,
            heading: "Flexibility",
            description: "The ability to switch is an important skill",
        },
        {
            logo: logo4,
            heading: "Solve the problem",
            description: "Code your way to a solution",
        }
    ]
    return (
        <div className="w-[410px] h-[432px] flex flex-col gap-8">
            {timelineData.map((item, index) => (
                <div key={index} className="flex flex-col gap-3 items-start">
                    <div  className="w-full h-[84px] gap-6 pt-4 pb-4 pr-3 pl-3 flex">
                        <div className="w-[52px] h-[52px] p-1 gap-1 flex items-center justify-center bg-white shadow-[0px_0px_62px_0px_#0000001F] rounded-[100%]">
                            <Image src={item.logo} alt={item.heading} width={16} height={16} />
                        </div>
                        <div className="w-[310px] h-[50px] gap-0.5 flex flex-col">
                            <p className="text-lg font-semibold leading-6.5 text-[var(--richblack-800)]">{item.heading}</p>
                            <p className="text-sm font-normal leading-5.5 text-[var(--richblack-700)]">{item.description}</p>
                        </div>
                    </div>
                    {index !== timelineData.length - 1 && <div className="w-[42px] h-0 border border-dashed -rotate-90 border-[#AFB2BF] ml-4 mt-4"/>}
                </div>
            ))}
        </div>
    );
}

export default TimeLine;